/* Code blocks */
pre[class*="language-"] {
  padding: 1em;
  margin: .5em 0;
  overflow: auto; }

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  background: #f5f2f0; }

/* Inline code */
:not(pre) > code[class*="language-"] {
  padding: .1em;
  border-radius: .3em;
  white-space: normal; }

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: slategray; }

.token.punctuation {
  color: #999; }

.namespace {
  opacity: .7; }

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #905; }

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #690; }

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #a67f59;
  background: rgba(255, 255, 255, 0.5); }

.token.atrule,
.token.attr-value,
.token.keyword {
  color: #07a; }

.token.function {
  color: #DD4A68; }

.token.regex,
.token.important,
.token.variable {
  color: #e90; }

.token.important,
.token.bold {
  font-weight: bold; }

.token.italic {
  font-style: italic; }

.token.entity {
  cursor: help; }

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
  margin: 0 0 1.5em;
  /* Make sure select elements fit in widgets. */ }
  .widget select {
    max-width: 100%; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
  display: block; }

.hentry {
  margin: 0 0 1.5em; }

.page-content,
.entry-content,
.entry-summary {
  margin: 1em 0 0; }

.entry-footer {
  font-size: 0.83rem; }

.page-links {
  clear: both;
  margin: 0 0 1.5em; }

.page-template-archives ul,
.error404 ul {
  margin-left: 0;
  padding-left: 0;
  list-style-type: none; }

/*--------------------------------------------------------------
## Books!
--------------------------------------------------------------*/
.dt_bookbox {
  font-size: 81%;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  align-items: start;
  grid-gap: 0.25vw 3vw; }
  .dt_bookbox h6, .dt_bookbox .dt_booklinks, .dt_bookbox .dt_bookbox_extra_comment {
    grid-column: 4 / span 7; }
  .dt_bookbox .dt_bookbox-image {
    grid-column: 1 / span 3;
    grid-row: 1 / span 3; }
  .dt_bookbox h6 {
    font-size: 1em;
    margin: 0; }
  @media (min-width: 37.5rem) {
    .dt_bookbox {
      font-size: 100%; }
      .dt_bookbox .dt_bookbox-image {
        grid-column: 1 / span 2; }
      .dt_bookbox h6, .dt_bookbox .dt_booklinks, .dt_bookbox .dt_bookbox_extra_comment {
        grid-column: 3 / span 8; } }
  @media (min-width: 60rem) {
    .dt_bookbox {
      grid-template-columns: repeat(8, 1fr); }
      .dt_bookbox .dt_bookbox-image {
        grid-column: 1 / span 2; }
      .dt_bookbox h6, .dt_bookbox .dt_booklinks, .dt_bookbox .dt_bookbox_extra_comment {
        grid-column: 3 / span 6; } }

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comment-content a {
  word-wrap: break-word; }

.comments-title span {
  font-style: italic; }

.comment-list {
  margin: 0;
  padding: 0; }

.comment-content {
  overflow-wrap: break-word;
  min-width: 0; }

.comment-meta .comment-author img {
  float: left;
  margin-right: 0.5em; }

.comment-meta .comment-author img, .comment-meta .comment-author .fn, .comment-meta .comment-author .says {
  vertical-align: middle; }

.comment-list article {
  padding: 0.5rem 0; }

.comment-list .even {
  background-color: #e6f2ff; }

.comment-body {
  border-bottom: 1px dotted #8d8c8c; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

.page-content img,
.entry-content img {
  width: 100%;
  height: auto; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
  margin-bottom: 1.5em;
  max-width: 100%; }
  .wp-caption img[class*="wp-image-"] {
    display: block;
    margin-left: auto;
    margin-right: auto; }
  .wp-caption .wp-caption-text {
    margin: 0.8075em 0; }

.wp-caption-text {
  text-align: center; }

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
  margin-bottom: 1.5em; }

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%; }
  .gallery-columns-2 .gallery-item {
    max-width: 50%; }
  .gallery-columns-3 .gallery-item {
    max-width: 33.33%; }
  .gallery-columns-4 .gallery-item {
    max-width: 25%; }
  .gallery-columns-5 .gallery-item {
    max-width: 20%; }
  .gallery-columns-6 .gallery-item {
    max-width: 16.66%; }
  .gallery-columns-7 .gallery-item {
    max-width: 14.28%; }
  .gallery-columns-8 .gallery-item {
    max-width: 12.5%; }
  .gallery-columns-9 .gallery-item {
    max-width: 11.11%; }

.gallery-caption {
  display: block; }

/*--------------------------------------------------------------
## Embeds
--------------------------------------------------------------*/
.fullwidth-embed {
  background: #4da6ff;
  padding: 1rem 0; }
  @supports (display: grid) {
    .fullwidth-embed {
      width: 100vw;
      margin-left: calc( ((100vw / 11) * 0.5 + 0.6vw ) * -1); } }
  .fullwidth-embed .twitter-tweet {
    margin: 0 auto;
    font-size: 1.18rem;
    color: #fff;
    font-style: italic; }
    @supports (display: grid) {
      .fullwidth-embed .twitter-tweet {
        margin: 0; } }
    .fullwidth-embed .twitter-tweet a {
      color: #fff; }

.entry-content blockquote.twitter-tweet {
  text-align: center; }

@supports (display: grid) {
  @media (min-width: 37.5rem) {
    .fullwidth-embed {
      margin-left: calc( ((100vw / 12) + 0.1vw  ) * -1); } }
  @media (min-width: 60rem) {
    .fullwidth-embed {
      margin-left: calc( ((100vw / 12) * 2  ) * -1); } } }
